export default {
	props: {
		fixedStyle: {
			type: String,
			default() {
				return 'position: fixed; top: 16rpx; right: 16rpx; z-index: 100;'
			}
		}
	},
	data() {
		return {
			isShowMask: false,
			animationShow: false
		}
	},
	methods: {
		
		toAddfriend() {
			uni.navigateTo({
				url: '/components/content/addfriend/AddFriend'
			})
		},
		
		
		
		//显示遮罩层
		tapShowMask() {
			this.animationShow = true
			this.isShowMask = true
		},
		
		//隐藏遮罩层
		tapHideMask(e) {
			this.animationShow = false
			setTimeout(() => {
				this.isShowMask = false
			}, 300)
		},
		
		
		
		
	}
}
